<template>
  <div>
    <Navi><em class="r">选择款式</em></Navi>
    <ul :class="$style.list">
      <li><label>产品型号</label><input type="text" :class="$style.in" placeholder="产品型号" /></li>
      <li><label>模型</label><input type="text" :class="$style.in" placeholder="模型" /></li>
      <li><label>款式型号</label><input type="text" :class="$style.in" placeholder="款式型号" /></li>
      <li><label>颜色</label>
      <div :class="$style.colors">
        <input type="radio" name="color" checked :class="[$style.rdo,$style.c1]" />
        <input type="radio" name="color" :class="[$style.rdo,$style.c2]" />
        <input type="radio" name="color" :class="[$style.rdo,$style.c3]" />
        <input type="radio" name="color" :class="[$style.rdo,$style.c4]" />
        <input type="radio" name="color" :class="[$style.rdo,$style.c5]" />
        <input type="radio" name="color" :class="[$style.rdo,$style.c6]" />
      </div>
      </li>
      <li><label>面料</label><input type="text" :class="$style.in" placeholder="面料" /></li>
      <li><label>里料</label><input type="text" :class="$style.in" placeholder="里料" /></li>
      <li><label>大底材质</label><input type="text" :class="$style.in" placeholder="大底材质" /></li>
      <li><label>制作工艺</label><input type="text" :class="$style.in" placeholder="制作工艺" /></li>
    </ul>
    <router-link to="/report-l"><Next/></router-link>
  </div>
</template>

<script>
import Navi from '@/components/Navi'
import Next from '@/components/Next'

export default {
  components : {
  	Navi,
    Next
  }
}
</script>

<style module>
@value colors: '../assets/value.css';
@value second from colors;

.list{padding:0 .3rem;margin-bottom:1rem;}
.list>li{line-height: .9rem;border-bottom:1px solid second;font-size: .28rem;}
.list label{display: inline-block;width: 1.6rem;padding-left:.1rem;}
.list .in{width: 3rem; line-height:.9rem;font-size: .32rem;outline: none; -webkit-appearance: none;border: none;}
.colors{position: relative;  display: inline-block;width:4rem;}
.list .rdo{width:.45rem;height:.45rem;outline: none;-webkit-appearance: none;border: none;border-radius:25%;box-sizing: content-box;position: absolute;top:-.3rem;margin: 0;}
.list .rdo:checked{border:.04rem solid #bbb;background-clip: content-box;padding: .05rem;transform: translate(-.1rem,-.1rem);}
.c1{background-color: #f3a536;left:0;}
.c2{background-color: #ce0b24;left:.8rem;}
.c3{background-color: #8a572e;left:1.6rem;}
.c4{background-color: #000;left:2.4rem;}
.c5{background-color: #437414;left:3.2rem;}
.c6{background-color: #4e92df;left:4rem;}

</style>


