import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/Index'
import Left from '@/pages/Left'
import Right from '@/pages/Right'
import Style from '@/pages/Style'
import ReportR from '@/pages/ReportR'
import ReportL from '@/pages/ReportL'
import Feedback from '@/pages/Feedback'
import Build from '@/pages/Build'
import Detail from '@/pages/Detail'
import Progress from '@/pages/Progress'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Index
    },
    {
      path: '/left',
      component: Left
    },
    {
      path: '/right',
      component: Right
    },
    {
      path: '/style',
      component: Style
    },
    {
      path: '/report-r',
      component: ReportR
    },
    {
      path: '/report-l',
      component: ReportL
    },
    {
      path: '/feedback',
      component: Feedback
    },
    {
      path: '/build',
      component: Build
    },
    {
      path: '/detail',
      component: Detail
    },
    {
      path: '/progress',
      component: Progress
    }
  ]
})



