<template>
  <div>
    <Navi><em class="r">贵宾资料</em></Navi>
    <ul :class="$style.list">
      <li><label>姓名</label><input type="text" placeholder="姓名" /></li>
      <li><label>性别</label><input type="text" placeholder="性别" /></li>
      <li><label>民族</label><input type="text" placeholder="民族" /></li>
      <li><label>年龄</label><input type="text" placeholder="您的年龄" /></li>
      <li><label>身高</label><input type="number" placeholder="您的身高" oninput="if(value.length>3)value=value.slice(0,3)" />CM</li>
      <li><label>体重</label><input type="number" placeholder="您的体重" oninput="if(value.length>3)value=value.slice(0,3)" />KG</li>
      <li><label>区域</label><input type="text" placeholder="你的区域" /></li>
      <li><label>职业</label><input type="text" placeholder="您的职业" /></li>
      <li><label>崇尚品牌</label><input type="text" placeholder="你崇尚的品牌" /></li>
      <li><label>贵宾号</label><input type="text" placeholder="您的贵宾号" /></li>
    </ul>
    <router-link to="/left"><Next/></router-link>
  </div>
</template>

<script>
import Navi from '@/components/Navi'
import Next from '@/components/Next'

export default {
  components : {
  	Navi,
    Next
  }
}
</script>

<style module>
@value colors: '../assets/value.css';
@value third from colors;

.list{padding:0 .3rem;margin-bottom:1rem;}
.list>li{line-height: .9rem;border-bottom:1px solid third;font-size: .28rem;}
.list label{display: inline-block;width: 1.6rem;padding-left:.1rem;}
.list input{width: 2rem; line-height:.9rem;font-size: .32rem;outline: none; -webkit-appearance: none;border: none;}
</style>


