<template>
  <div :class="$style.box">
    <router-link to="/" class="l"><img src="~pic/logo.png" :class="$style.logo"></router-link>
    <slot></slot>
  </div>
</template>

<script>
export default {
  
}
</script>

<style module>
@value colors: '../assets/value.css';
@value primary from colors;

.box{height:.86rem;background: primary;padding:0 .3rem;line-height: 1;}
.box :global .l{width: 2rem;height: .6rem; transform: translate(0,25%);}
.logo{width: 100%; height: 100%;}
.box :global .r{color: #fff;margin:.28rem .6rem 0 0;}
.box :global .r:after{content: '';position: absolute;background: url(~pic/logo.png) no-repeat;background-size:contain;width: .5rem;height: .5rem;margin:-.1rem .2rem;}
</style>


