.top{background: primary;}
.hd{font-size: .32rem;padding:.6rem 0 .5rem;text-align: center;}

.in{width:6.3rem;height:2rem;display: block;margin:0 auto 1.2rem; font-size: .32rem;  border-radius: 0.2rem;border: 1px solid third;box-sizing: border-box;padding:.2rem;background: #f6f6f6;}
